// app/dashboard/page.tsx
"use client";

import { useState, useEffect } from "react";

interface User {
  _id: string;
  username: string;
  isActive: string;
  lastActiveText: string;
}

export default function Dashboard() {
  const [users, setActiveUsers] = useState<User[]>([]);

  useEffect(() => {
    const fetchActiveUsers = async (): Promise<void> => {
      try {
        const response = await fetch(
          "http://localhost:5000/api/users/active-users"
        );
        if (response.ok) {
          const data = await response.json();
          setActiveUsers(data);
        } else {
          throw new Error("Failed to fetch active users");
        }
      } catch (error) {
        console.error(error);
      }
    };

    fetchActiveUsers();
  }, []);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100">
      <h1 className="text-4xl font-bold mb-6">Welcome to the Dashboard</h1>
      <p className="text-lg text-gray-700">You have successfully logged in!</p>

      <div className="mt-8">
        <h2 className="text-2xl font-semibold mb-4">Active Users</h2>
        <ul className="space-y-2">
          {users.map((user) => (
            <li
              key={user._id}
              className="text-gray-800 bg-white p-3 rounded shadow"
            >
              <button>
                <span className="text-red-900 inline-block p-1">
                  {user.username} - {user.isActive ? "Online" : "Offline"}
                </span>
                <span className="text-blue-300">
                  LastActive {user.lastActiveText}
                </span>
              </button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
